.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.3;
}

.sidebar__header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar__headerright{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.sidebar__search{
    display: flex;
    align-items: center;
    height: 40px;
    padding: 10px;
}

.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar__searchContainer> .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}

.sidebar__searchContainer>input{
    border:none;
    margin-left: 10px;
}

.sidebar__chat{
    flex: 1;
    background-color: white;
    overflow: scroll;
    overflow-x: hidden;
    
}

