.sidebarchat{
    display: flex;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6 ;
    align-items: center;
}

.sidebarchat:hover{
    background-color: #ebebeb;
}

.sidebarchat__info{
    margin-left: 15px;
}
.sidebarchat__info>h2{
    font-size: 16px;
    margin-bottom: 8px;
}
.sidebarchat__info>p{
    font-size: 15px;
}